import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialogModule, MAT_DIALOG_DEFAULT_OPTIONS } from '@angular/material/dialog';

@Component({
  selector: 'app-change-password-dialog',
  templateUrl: './change-password-dialog.component.html',
  styleUrls: ['./change-password-dialog.component.scss']
})
export class ChangePasswordDialogComponent implements OnInit {

  public title: string;
  public error: string;
  public message: string;
  public placeHolder_1: string;
  public placeHolder_2: string;
  public newpwd: string;
  public newpwd2: string;
  public buttonCancel: string;
  public changeButton: string;
  public showError = false;
  
  constructor(

    public mdDialogRef: MatDialogRef<ChangePasswordDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) {
    this.error = '';
    this.title = data.Title;
    this.message = data.Message;
    this.placeHolder_1 = data.PlaceHolder_1;
    this.placeHolder_2 = data.PlaceHolder_2;
    this.newpwd = this.newpwd2 = '';

    const currentBrowserLanguage = navigator.language.toLocaleLowerCase();

    if (currentBrowserLanguage.startsWith('it')) {
      this.buttonCancel = 'ANNULLA';
      this.changeButton = "CAMBIA";
      this.error = 'Le password devono coincidere';
    } else {
      this.buttonCancel = 'CANCEL';
      this.changeButton = "CHANGE";
      this.error = 'Passwords must match';
    }
  }

  ngOnInit() { }
  public cancel() {
    this.showError = false;
    this.data = undefined;
    this.mdDialogRef.close(this.data);
  }

  public confirm() {
    this.showError = false;
    if (this.newpwd === this.newpwd2 && this.newpwd2 !== '' && this.newpwd2 !== undefined) {
      this.data.NewPwd = this.newpwd;
      this.mdDialogRef.close(this.data);
    } else {
      this.showError = true;
    }
  }
}
